<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">项目权限（{{total}}）</div>
        <el-input placeholder="搜索" v-model="searchText" class="header-right-time right" :clearable='true' @change="initData()">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="historyapply-group-tablebox table-group-tablebox">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <th>序号</th>
                  <th>项目名称</th>
                  <th>管理人</th>
                  <th>项目状态</th>
                  <th>操作</th>
                </tr>
                <tr v-for="(item,index) in datalist" :key="item.seq">
                  <td>{{pageSize*(currentPage-1)+index+1}}</td>
                  <td>{{item.name}}</td>
                  <td>{{item.user_name}}</td>
                  <td>{{item.status}}</td>
                  <td>
                    <span class="table-link" @click="toTransferManage(item.company_id,item.name)">{{item.user_id!=null?'已分配':'分配管理人'}}</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="total==0">
                <img src="../../images/table_nodata.png"> 没有对应数据
              </div>
            </div>
            <div class="pagination-normal" v-if="total!=0">
              <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="getListData" :current-page="currentPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <!--                         项目管理权限分配弹窗                  -->
      <!-- 文件上传弹窗 -->
      <el-dialog title="项目管理权限分配" :visible.sync="changePermissions" width="500px" center :append-to-body='true'>
        <div class="dialog_content">
          <div class="form-group">
            <div class="clear peopleSelectTitle">
              <div class="left peopleSelectTitle-text">选择管理人</div>
              <el-select class="right peopleSelectTitle-select" v-model="value7" filterable placeholder="请选择" @change="changemanage($event)">
                <el-option-group v-for="group in peoplelist" :key="group.name" :label="group.name">
                  <el-option v-for="item in group.children" :key="item.id" :label="item.name" :value="item.id">
                  </el-option>
                </el-option-group>
              </el-select>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog_footer clear">
          <button class="dialog-btn-submit btn-blue" @click="submitClick()">提交</button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import { settingcCompanyList, getUserCompany, setCompanyUser } from '../../config/api'
import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
export default {
  data() {
    return {
      currentPage: 1,
      total: 10,
      pageSize: 10,
      searchText: '', //查询数据
      datalist: [], //项目列表
      selectAll: false, //是否全选
      changePermissions: false, //弹窗显示
      peoplelist: [], //人员列表
      value7: '', //已选中人员
      checkedpersonid: -1,
      submitType: -1, //修改方式（批量0，单个-1）
      selectedProjectId: -1, //单个修改选中的项目id
    }
  },
  created() {
    

    let page = 1;
  	if (this.$route.query.page) {
  		page = parseInt(this.$route.query.page);
  	}
    this.currentPage = page
    this.initData();
    this.getPeopleList();
  },
  
  computed: {
    selectedProject: function() {
      let sp = [];
      this.datalist.forEach(item => {
        if (item.checked) {
          sp.push(item.company_id);
        }
      })
      return sp;
    }
  },
  mounted() {},
  methods: {
    initData() {
      this.selectAll = false;
      settingcCompanyList({
        number: this.pageSize,
        page: this.currentPage,
        search: this.searchText
      }).then(res => {
        if (res.data.msgNo == 1) {
          let dl = res.data.payload.data;
          dl.forEach(item => {
            item.checked = false;
            if (item.user_id == null) {
              item.user_name = '---';
            }
          })

          this.datalist = res.data.payload.data;
          this.total = res.data.payload.total;
        }
      })
    },
    getPeopleList() {
      getUserCompany().then(res => {
        if (res.data.msgNo == 1) {
          this.peoplelist = res.data.payload;
        }
      })
    },
    getListData(val) {
      this.datalist = [];
      this.currentPage = val;
      this.initData();
    },
    changemanage(e) {
      this.checkedpersonid = e;
    },
    //全选按钮
    selectallclick(e) {
      this.datalist.forEach(item => {
        if (e && item.user_id == null) {
          item.checked = e
        } else if (!e) {
          item.checked = e;
        }
      })
    },
    //单选按钮
    selectoneclick() {
      let count = 0;
      let count1 = 0;
      this.datalist.forEach(item => {
        if (item.user_id == null) {
          count1++
        }
        if (item.checked) {
          count++
        }
      })
      if (count != 0 && count == count1) {
        this.selectAll = true
      } else {
        this.selectAll = false
      }
    },
    transferManage(id) {
      this.submitType = id;
      if (id != -1) {
        this.selectedProjectId = id;
        this.changePermissions = true;
      } else {
        if (this.selectedProject.length == 0) {
          dialogConfirm('info', '您还没有选择项目')
        } else {
          this.changePermissions = true;
        }
      }

    },
    toTransferManage(id, name) {
      this.$router.push({
        path: '/home/projectPermissionsToSet',
        query: {
          id: id,
          page: this.currentPage,
          name: encodeURIComponent(name),
          
        }
      })
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.table-group-title-btn {
  line-height: 30px;
  font-size: 14px;
  padding: 0px 10px;
  margin-right: 32px;
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
    float: left;
    margin-top: 5px;
    margin-right: 5px;
    background: url('../../images/projectpermissions_icon.png') no-repeat
  }
}

.peopleSelectTitle-text {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 40px;
  margin-right: 10px;
}

.peopleSelectTitle {
  border-bottom: 1px solid #dcdfe6;
}

</style>



